import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import {blue} from '@material-ui/core/colors';
import {TextField, Typography, Button} from "@material-ui/core";
import HelpIcon from '@material-ui/icons/Help';
import {post_data} from "../../ApiCall";
import {BACK_END_URL} from "../../ServiceURL";
import ImageDialog from "../ImageDialog";

const useStyles = makeStyles({
    avatar: {
        backgroundColor: blue[100],
        color: blue[600],
    },
    container: {
        padding: '1rem',
        minWidth: '500px'
    },
    input: {
        width: '100%'
    },
    icon: {
        width: '100%'
    },
    footer: {
        display: 'flex',
        flexDirection: 'row',
        marginTop: '1rem',
        justifyContent: 'flex-end'
    }
});
export default function AddCategoryDialog(props) {
    const {onClose, refresh_list, open, parent} = props;
    const classes = useStyles();
    const [state, setState] = useState({
        name: '',
        icon_class: ''
    })

    const handleAddCategory = () => {
        post_data(BACK_END_URL +'/admin/sub_category', {}, {
            name : state.name,
            category_id: parent.id,
            banner : bannerSrc,
            icon: iconSrc
        }, true)
            .then(res => {
                refresh_list()
                onClose()
            })
            .catch(error => {})
    };
    const handleChange = (e) => {
        let name = e.target.name
        let val = e.target.value
        setState({
            ...state,
            [name]: val
        })
    }
    const [iconSrc, setIconSrc] = useState(null)
    const [bannerSrc, setBannerSrc] = useState(null)
    const [openIcon, setOpenIcon] = useState(false)
    const [dataIcon, setDataIcon] = useState({
        name: null,
        src: null,
        aspect: 1/1,
    })
    const handleIconChange = (e) => {
        const file = e.target.files[0]
        let url = URL.createObjectURL(file)
        setDataIcon({
            src: url,
            aspect: 1/1
        })
        setOpenIcon(true)
    }
    const handleSetIcon = (e => {
        let name = e.name
        let value = e.url_image
        setIconSrc(value)
    })

    const [openBanner, setOpenBanner] = useState(false)
    const [dataBanner, setDataBanner] = useState({
        name: null,
        src: null,
        aspect: 3.5/1,
    })
    const handleBannerChange = (e) => {
        const file = e.target.files[0]
        let url = URL.createObjectURL(file)
        setDataBanner({
            src: url,
            aspect: 3.5/1
        })
        setOpenBanner(true)
    }
    const handleSetBanner = (e => {
        let name = e.name
        let value = e.url_image
        setBannerSrc(value)
    })
    return (
        <Dialog aria-labelledby="add-category" open={open}>
            <DialogTitle id = "add-category">Add Category</DialogTitle>
            <div className={classes.container}>
                <Typography style={{marginTop: '1rem', marginBottom: '0.5rem'}}>Name sub-category:</Typography>
                <TextField variant={'outlined'} placeholder={'Name Category'} required={true}
                           name='name' value={state.name} onChange={handleChange} className={classes.input}></TextField>
                <span>Icon: </span>
                <label htmlFor={'input-icon-pcat'}>Upload</label>
                <div style={{
                    width: '3em',
                    height: '3em',
                    border: iconSrc ? 'none' : '1px dashed black',
                    marginTop: '1em',
                    marginBottom: '1em',
                    backgroundImage: iconSrc ? `url(${iconSrc})` : 'none',
                    backgroundSize: 'contain',
                    backgroundRepeat: 'no-repeat'
                }}>

                </div>
                <input type='file' id="input-icon-pcat" style={{display: "none"}} onChange={handleIconChange}/>
                <span>Banner: </span>
                <input type='file' id='input-banner-pcat' style={{display: "none"}} onChange={handleBannerChange}/>
                <label htmlFor='input-banner-pcat'>Upload</label>
                <div style={{
                    width: '100%',
                    height: '9em',
                    border: iconSrc ? 'none' : '1px dashed black',
                    marginTop: '1em',
                    backgroundImage: bannerSrc ? `url(${bannerSrc})` : 'none',
                    backgroundSize: 'contain',
                    backgroundRepeat: 'no-repeat'
                }}>
                </div>
                <div className={classes.footer}>
                    <Button variant={'contained'} color={'primary'} onClick={handleAddCategory}>Save</Button>
                    <Button style={{marginLeft: '0.5rem'}} onClick={onClose}>Cancel</Button>
                </div>
            </div>
            <ImageDialog data={dataIcon} open={openIcon} setPImage={handleSetIcon} onClose = {()=>{setOpenIcon(false)}} />
            <ImageDialog data={dataBanner} open={openBanner} setPImage={handleSetBanner} onClose = {()=>{setOpenBanner(false)}} />
        </Dialog>
    );
}