import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import {Box, IconButton} from "@material-ui/core";
import EditIcon from '@material-ui/icons/Edit';
import RestoreFromTrashIcon from '@material-ui/icons/RestoreFromTrash';
import '@fortawesome/fontawesome-free/css/all.css'
import '@fortawesome/fontawesome-free/js/all'
import {delete_data} from "../../ApiCall";
import {BACK_END_URL} from "../../ServiceURL";
import {Dialog, DialogTitle, Typography} from '@material-ui/core'
import DeleteDialog from "../Dialog.Delete";
import EditCategoryDialog from "./Dialog.EditCategory";
const useStyles = makeStyles({
    table: {
        minWidth: 500,
        margin : "3rem auto",
    },
});


export default function CategoryTable({data, refresh_list}) {
    const classes = useStyles();
    const deleteCategory = ()=>{
        let sub_category_id = itemDelete
        setOpenDeleteDialog(false)
        delete_data(BACK_END_URL + `/admin/sub_category/${sub_category_id}`, {}, true)
            .then(res=>refresh_list())
            .catch(error => alert('Error while delete! Please make sure sub_category is None and list product of sub_category is None'))
    }
    const [openDeleteDialog, setOpenDeleteDialog] = React.useState(false)
    const [itemDelete, setItemDelete] = React.useState(0)
    const [openEditDialog, setOpenEditDialog] = React.useState(false)
    const [dataEdit, setDataEdit] = React.useState({})
    return (
        <TableContainer component={Box}>
            <Table className={classes.table} aria-label="simple table">
                <TableHead>
                    <TableRow>
                        <TableCell style={{fontWeight : "bold"}}>Id</TableCell>
                        <TableCell style={{fontWeight : "bold"}}>Name</TableCell>
                        <TableCell>
                            Icon
                        </TableCell>
                        <TableCell>
                            Banner
                        </TableCell>
                        <TableCell align={"center"} style={{fontWeight : "bold", width :"15rem"}}>Action</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {data.map((row) => (
                        <TableRow key={row.id}>
                            <TableCell>{row.id}</TableCell>
                            <TableCell>
                                {row.name}
                            </TableCell>
                            <TableCell>
                                <img src={row.icon} style={{height : '4em'}}/>
                            </TableCell>
                            <TableCell>
                                <img src={row.banner} style={{height : '4em'}}/>
                            </TableCell>
                            <TableCell align="center">
                                <div style={{display: "flex", flexDirection : "row", justifyContent : 'center'}}>
                                    <IconButton style={{color: '#4caf50'}} onClick={(e)=>{
                                        setOpenEditDialog(true)
                                        setDataEdit(row)
                                       }}>
                                        <EditIcon/>
                                    </IconButton>
                                    <IconButton style={{color: '#e91e63'}} onClick={(e)=>{
                                        setOpenDeleteDialog(true)
                                        setItemDelete(row.id)
                                       }}>
                                        <RestoreFromTrashIcon/>
                                    </IconButton>
                                </div>
                            </TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
            <DeleteDialog open = {openDeleteDialog} onClose = {()=> setOpenDeleteDialog(false)} handleDelete = {deleteCategory}/>
            <EditCategoryDialog open={openEditDialog} onClose={() => setOpenEditDialog(false)}
                                  refresh_list={refresh_list} data = {dataEdit}/>
        </TableContainer>
    );
}