import React, {useEffect, useState} from 'react';
import {FormControl, MenuItem, Select, Typography, Button} from "@material-ui/core";
import AddIcon from '@material-ui/icons/Add';
import {get_data} from "../ApiCall";
import {BACK_END_URL} from "../ServiceURL";
import SimpleTable from "../Component/ParentCategory/Table.ParCategory";
import SimpleDialog from "../Component/ParentCategory/Dialog.AddParCategory"
import CategoryTable from "../Component/Category/Table.Category";
import AddCategoryDialog from "../Component/Category/Dialog.AddCategory";

const CategoryPage = () => {
    const [parcategoryList, setparcategoryList] = useState([])
    const [parcategory, setParCategory] = useState(0)
    const [category, setCategory] = useState([])
    const [openAddDialog, setOpenAddDialog] = useState(false)
    const [parent, setParent] = useState({
        category_id : 0,
        category_name: ''
    })
    useEffect(() => {
        get_data(BACK_END_URL + '/public/category', {page: 1}, false)
            .then(res => {
                setparcategoryList(res.data.list)
            })
    }, [])
    useEffect(() => {
        get_data(BACK_END_URL + '/public/sub_category', {category_id: parcategory}, false)
            .then(res => {
                setCategory(res.data.list)
                setParent(res.data.category)
            })
            .catch(error => setCategory([]))
    }, [parcategory])
    const handleChange = (e) => {
        setParCategory(e.target.value)
    }
    const refresh_list = () => {
        get_data(BACK_END_URL + '/public/sub_category', {category_id: parcategory}, false)
            .then(res => setCategory(res.data.list))
            .catch(error => setCategory([]))
    }
    const addCategory = () => {
        setOpenAddDialog(true)
    }

    return (
        <div style={{padding: '2rem'}}>
            <Typography variant={'h4'}>SubCategory</Typography>
            <div style={{width: "100%", marginTop: '1.5rem', display: 'flex', flexDirection: 'row'}}>
                <FormControl variant="outlined" required>
                    <Select
                        name="category"
                        value={parcategory}
                        onChange={handleChange}
                    >
                        <MenuItem value={0} disabled={true}>Select a category</MenuItem>
                        {
                            parcategoryList.map((item) => <MenuItem key={item.id}
                                                                 value={item.id}>{item.name}</MenuItem>)
                        }
                    </Select>
                </FormControl>
                <div style={{flexGrow: 1}}></div>
                {
                    parcategory === 0? null:
                    <Button color={'primary'} variant={'contained'} onClick={addCategory}> <AddIcon/>Add
                        Subcategory</Button>
                }
            </div>
            <CategoryTable data={category} refresh_list={refresh_list} parent = {parent}/>
            <AddCategoryDialog open={openAddDialog} onClose={() => setOpenAddDialog(false)}
                                  refresh_list={refresh_list} parent={parent}/>
        </div>
    );
};

export default CategoryPage;